const bios = document.querySelector("#bio");
const work = document.querySelector("#work");
const skills = document.querySelector("#skills");
document.querySelector("#bioswitcher").addEventListener("click",()=>{
    let bcl = bios.classList;
    let wcl = work.classList;
    let scl = skills.classList;
    if (bcl.contains("hidden")){
        bcl.remove("hidden");
        wcl.add("hidden");
        scl.add("hidden");
    }
});   
document.querySelector("#workswitcher").addEventListener("click",()=>{
    let bcl = bios.classList;
    let wcl = work.classList;
    let scl = skills.classList;
    if (wcl.contains("hidden")){
        wcl.remove("hidden");
        bcl.add("hidden");
        scl.add("hidden");
    }
});
document.querySelector("#skillswitcher").addEventListener("click",()=>{
    let bcl = bios.classList;
    let wcl = work.classList;
    let scl = skills.classList;
    if (scl.contains("hidden")){
        scl.remove("hidden");
        bcl.add("hidden");
        wcl.add("hidden");
    }
});